import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  //mode: "history",
  scrollBehavior() {
    return window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  },
  routes: [
    // Pages
    {
      path: "/",
      redirect: "home",
    },
    {
      path: "/home",
      name: "home",
      component: () => import("../components/Content.vue"),
    },
    {
      path: "/download",
      name: "download",
      component: () => import("../components/Content.vue"),
    },
  ],
});
